import "./styles.css";
import { useState } from "react";

export default function App() {
  const [meaning, setMeaning] = useState("");
  const emojiDictionary = {
    Grinning_Face: "😀😀😀",
    Laughing: "🤣🤣🤣",
    Face_with_Tear_of_Joy: "😂😂😂",
    Winking_Face: "😉😉😉",
    Smiling_Face_with_Hallo: "😇😇😇",
    Smiling_Face_with_Hearts: "🥰🥰🥰",
    Smiling_Face_with_Heart_Eyes: "😍😍😍",
    Star_Struck: "🤩🤩🤩",
    Face_Blowing_kiss: "😘😘😘",
    Kissing: "😗😗😗",
    Monkey: "🐒🐒🐒"
  };

  const animalNames = Object.keys(emojiDictionary);

  const emojiSearchHandler = function (event) {
    const animalName = event.target.value.toLowerCase();
    const result = emojiDictionary[animalName];
    if (animalName === "") {
      setMeaning(
        "Enter any emoji from the list or click the desired emoji button."
      );
    } else {
      if (result === undefined) {
        setMeaning("Not Found");
      } else {
        setMeaning(result);
      }
    }
  };

  const emojiClickHandler = function (animal) {
    const result = emojiDictionary[animal.animal];
    setMeaning(result);
  };

  return (
    <div className="App">
      <h1>Welcome to World of Emojiz!!</h1>
      <input onChange={emojiSearchHandler} />
      <p className="meaning">{meaning}</p>
      <div className="animals">
        {animalNames.map((animal) => {
          return (
            <button key={animal} onClick={() => emojiClickHandler({ animal })}>
              {animal}
            </button>
          );
        })}
      </div>
    </div>
  );
}
